import React from 'react';
import GenericButton from './GenericButton';
import StartButton from './StartButton';

const codeStyle = {
  fontSize: '10vw',
  backgroundColor: 'var(--my-yellow)',
  borderRadius: '10px'
};

export default function HostScreen(props) {
  const playersList = props.playersList.map((el) => {
    return <li key={el}>{el}</li>;
  });

  return (
    <div>
      <GenericButton text={'‚è™Back'} handleClick={props.goBack} />
      <div style={codeStyle}>{props.gameCode}</div>
      <ul>{playersList}</ul>
      <StartButton handleClick={props.beginHost} text={'START GAME‚ú®'} />
    </div>
  );
}
